/* Assicura che il contenitore sia in posizione relativa */
.img-service,
.layers {
  position: relative;
}

/* Immagine logo: posizionata in assoluto, copre l'intera card, inizialmente nascosta */
.bg-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
  z-index: 1;
  transition: opacity 0.3s ease-out;
}

/* Quando si passa il mouse sulla card, il logo diventa visibile */
.img-service:hover .bg-image {
  opacity: 1;
}

/* Immagine del membro: posizione relativa per stare sopra il logo */
.member-image {
  position: relative;
  z-index: 2;
}

.image-overlay {
  z-index: 3;
}

.card-img-overlay::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0); /* inizialmente trasparente */
  transition: background 0.3s ease;
  z-index: 1;
}

/* Assicurati che il contenuto (testo, bottoni, ecc.) stia sopra l'overlay */
.card-img-overlay > * {
  position: relative;
  z-index: 2;
}

/* Al passaggio del mouse sulla card, attiva l'overlay scuro */
.img-service:hover .card-img-overlay::before {
  background: rgba(0, 0, 0, 0.3);
}


/*
  TIMELINE
*/
ul.timeline-3 {
  list-style-type: none;
  position: relative;
}
ul.timeline-3:before {
  content: " ";
  background: #d4d9df;
  display: inline-block;
  position: absolute;
  left: 29px;
  width: 2px;
  height: 100%;
  z-index: 400;
}
ul.timeline-3 > li {
  margin: 20px 0;
  padding-left: 20px;
}
ul.timeline-3 > li:before {
  content: " ";
  background: white;
  display: inline-block;
  position: absolute;
  border-radius: 50%;
  border: 3px solid #0a822e;
  left: 20px;
  width: 20px;
  height: 20px;
  z-index: 400;
}

.square-img {
  aspect-ratio: 1 / 1;
  width: 100%;
  object-fit: cover;
}